import {
	uniIcons,
	uniPopup ,
	uniNavBar
} from '@dcloudio/uni-ui';
import simpleDatetimePicker from '@buuug7/uniapp-components/lib/simple-datetime-picker';

export default {
	components: {
		uniIcons,
		uniNavBar,
		uniPopup ,
		simpleDatetimePicker
	},
	data(){
		return{
			statusDay:"2020-03-02 0:0",
			endDay:'2020-03-04 23:59',
			startTime:'',
			endTime:'',
			second:86400000,
			fives:18000000,
			twenty:82800000,
			executionNum:0,
			Token:uni.getStorageSync('userId') || '',
			isAgain:false,
			ismodel:false,
			ismolnum:1,
			paytype:[],
			idArr:[],
			types: {
				collect: [],
				branch: []
			},
			dataobj:{
				genre:1,
				amount:"",
				useDate:"",
				payType:{},
				useType:{},
				remarks:"",
				picture:""
			},
			typeitems:[]
		}
	},
	onLoad: function(option) {
		this.paytype=[ ...this.GlobalJson.paytype];
		
		if(option.item){
			let _dataobj = JSON.parse(option.item);
			console.log('_dataobj:',_dataobj)
			this.dataobj=_dataobj
		}else{
			this.dataobj.useDate=this.utils.Format(new Date());
		}
	},
	onShow() {
		this.gettypelist();
	},
	methods: {
		//返回上一页面
		toback() {
			if(this.dataobj._id){
				uni.redirectTo({
				    url: './details?id=' + this.dataobj._id
				});
			}else{
				uni.navigateBack({
					delta: 1
				});
			}
		},
		
		// 创建事件start
		firstAgin(){
			this.startTime=new Date();
			this.first();
		},
		first(){
			const _this=this;
			let userdate=(new Date((new Date(this.statusDay).getTime()+this.second)))
			this.statusDay=this.utils.Format(userdate);
			console.log("=====================================",this.statusDay)
			// console.log(new Date(this.endDay).getTime(),new Date(this.statusDay).getTime())
			if(new Date(this.endDay).getTime()<new Date(this.statusDay).getTime()){
				this.endTime=new Date();
				let second = parseInt((new Date().getTime() - this.startTime.getTime()) / 1000);
				let waste = this.utils.formatSeconds(second)
				console.log("共执行"+this.executionNum+"次任务,耗时"+waste)
				console.log(this.idArr)
				uni.showModal({
				    title: '提示',
					mask:true,
				    content: '任务完成',
				    success: function (res) {
				        if (res.confirm) {
				            console.log('用户点击确定');
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			}else{
				let _num = this.utils.randomNum(5,9),ind=0;
				let t1=new Date(this.statusDay).getTime()+this.fives;
				let t2=new Date(this.statusDay).getTime()+this.twenty;
				
				let timer= setInterval(()=>{
					if(ind<_num){
						ind++;
						let useDate= this.utils.randomNum(t1,t2);
						_this.start(new Date(useDate));
					}else{
						clearInterval(timer);
						_this.first()
					}
				},1200)
			}
		},
		start(useDate){
			let val= this.utils.randomNum(1,2);
			let num = this.utils.randomNum(1,13);
			this.dataobj.genre=val;
			// this.dataobj.createTime=useDate.getTime();
			this.dataobj.amount=this.utils.randomNum(0.01,40.99);
			this.dataobj.remarks=num>5?Math.random().toString(36).slice(-num):'';
			this.dataobj.picture='../../static/fapiao.jpg';
			if(val == 1){
				this.dataobj.useType=this.types.collect[this.utils.randomNum(0,this.types.collect.length-1)];
			}else{
				this.dataobj.useType=this.types.branch[this.utils.randomNum(0,this.types.branch.length-1)];
			}
			this.dataobj.payType=this.paytype[this.utils.randomNum(0,this.paytype.length-1)];
			this.dataobj.useDate=this.utils.Format(useDate);
			this.saveData();
		},
		// 创建数据 end
		
		
		//切换收入支出
		handleexin(val){
			this.$refs.popup.close();
			this.dataobj.genre=val;
			this.gettypelist();
			if(this.dataobj._id){
				this.dataobj.useType={};
			}else{
				let _Obj={
					genre:val,
					amount:"",
					useDate:"",
					payType:{},
					useType:{},
					remarks:""
				};
				_Obj.useDate=this.utils.Format(new Date());
				this.dataobj=_Obj;
			}
		},
		// 获取类别数据
		gettypelist() {
			uni.showLoading({
				title: '数据加载中...',
				mask: true
			})
			uniCloud.callFunction({
				name: 'settype',
				data: {
					pay: 4,
					token: this.Token,
					genre: this.dataobj.genre
				}
			}).then((res) => {
				uni.hideLoading();
				let more=[{
					iconclass: "iconjia",
					label:'编辑',
					isadd:true
				}];
				if (res.result.status == 0) {
					this.isnull = false;
					let _data = [...res.result.list.data];
					
					 this.typeitems =[..._data,...more];
					 console.log('typeitems:',this.typeitems)
				} else {
					this.typeitems = [...more];
					uni.showToast({
						title: res.result.msg,
						icon: 'none'
					})
				}
			}).catch((err) => {
				uni.hideLoading();
				uni.showModal({
					content: `操作失败，请重新操作`,
					showCancel: false
				})
			})
		},
		// 弹出模态框 
		showModel(val){
			this.ismolnum=val;
			this.$refs.popup.open()
		},
		// 上传文件
		upload() {
			new Promise((resolve, reject) => {
				uni.chooseImage({
					chooseImage: 1,
					success: res => {
						const path = res.tempFilePaths[0]
						const options = {
							filePath: path
						}
						resolve(options)
					},
					fail: () => {
						reject(new Error('Fail_Cancel'))
					}
				})
			}).then((options) => {
				uni.showLoading({
					title: '文件上传中...'
				})
				return uniCloud.uploadFile(options)
			}).then(res => {
				uni.hideLoading()
				uni.showToast({
					title:'图片上传成功',
					mask:true
				})
				this.dataobj.picture = res.fileID;
			}).catch((err) => {
				uni.hideLoading()
				console.log(err);
				if (err.message !== 'Fail_Cancel') {
					uni.showModal({
						content: `图片上传失败，请重新上传`,
						showCancel: false
					})
				}
			})
		},
		// 查看文件
		seeimgsrc(){
			this.$refs.picture.open();
		},
		// 删除当前文件
		closeimgSrc(){
			this.dataobj.picture='';
		},
		// 保存数据
		saveData(){
	
			  if(!this.dataobj.amount){
				  uni.showToast({
				  	title:'请输入金额',
					icon:'none'
				  })
				  return
			  }
			  if(!this.dataobj.payType.label){
				  uni.showToast({
					title:'请选择方式',
					icon:'none'
				  })
				  return
			  }
			  if(!this.dataobj.useType.label){
			  	uni.showToast({
			  	    title:'请选择方式',
					icon:'none'
			    })
			    return
			  }
			  uni.showLoading({
			    title: '数据保存中...',
			  	mask:true
			  })
			  let _data={...this.dataobj},eventName='',_this=this;
			  _data.useDategetTime=new Date(_data.useDate).getTime();
			  let dateArr=_data.useDate.split(' ');
			  let dateArr2 = dateArr[0].split('-');
			  _data.useYear=dateArr2[0];
			  	_data.useMonth=dateArr2[1];
			  	_data.useDay=dateArr2[2];
				_data.useHour=dateArr[1];
				_data.token=this.Token;
			  if(_data._id){
				  eventName="update";
			  }else{
				  eventName="add";
			  }
	
			  uniCloud.callFunction({
			    name: eventName,
			    data: _data
			  }).then((res) => {
				  console.log(res)
				  if(res.result.id){
					  this.idArr.push(res.result.id);
					    this.executionNum++;
					  uni.hideLoading()
					  uni.showToast({
					  	title:'数据保存成功',
					  	mask:true,
					  	duration:2000
					  })
					  if(_data.dataId){
					  	_data._id=_data.dataId;
					  	delete _data.dataId;
					  	try {
					  	    uni.setStorageSync('editData', _data);
					  	} catch (e) {
					  	    // error
					  	}
					  }
					  setTimeout(()=>{
					  	_this.toback();
					  },2500)
				  }else{
					  uni.showToast({
					  	title:'保存失败，请重新输入',
						icon:'none',
					  	mask:true,
					  	duration:2000
					  })
				  }
			  }).catch((err) => {
			    uni.hideLoading()
			    uni.showToast({
			    	title:'数据保存失败，请重新保存',
					mask:true,
					icon :'none',
			    	duration:2000
			    })
			    console.error(err)
			  })
		},
		//选择某个pay方式 
		handlepay(item){
			this.dataobj.payType=item;
			this.$refs.popup.close();
		},
		// 选择某个类别
		handleType(item){
			this.$refs.popup.close();
			if(item.isadd){
				uni.navigateTo({
					url: './typelist?genre='+this.dataobj.genre
				});
			}else{
				this.dataobj.useType=item;
			}
		},
		// 打开picker
		openDatetimePicker() {
			this.$refs.myPicker.show();
		},
		// 关闭picker
		closeDatetimePicker() {
			this.$refs.myPicker.hide();
		},
		// 确定选择的日期时间
		handleSubmit(e) {
			// console.log(e); // {year: "2019", month: "07", day: "17", hour: "15", minute: "21"}
			if (e.year && e.month && e.day) {
				this.dataobj.useDate = `${e.year}-${e.month}-${e.day} ${e.hour}:${e.minute}`;
			}
		}
	}
}
